import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			cateMaskState: 0,
			headerPosition:"fixed",
			headerTop:"0px",
			loadingType: 'more',
			filterIndex: 0, 
			cateId: 0,
			pcateId:0,
			priceOrder: 0,
			cateList: [],
			goodsList: [],
			count:'',
			pn:0,
			bondedwh_id:0,
			yhqid:0,
			yhqDetail:[],
			shopname:''
			
		};
	},
	onLoad(options){



		if(options.tid){
			this.cateId = options.tid;
		}
		if(options.sid){
			this.pcateId = options.sid;
		}
		if(options.bondedwh_id){
			this.bondedwh_id = options.bondedwh_id;
		}
		if(options.yhqid){
			this.yhqid = options.yhqid;
		}
		if(options.shopname){
			this.shopname = options.shopname;
			uni.setNavigationBarTitle({
			    title: options.shopname
			});
		}
		this.loadData();
	},
	onPageScroll(e){
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	onPullDownRefresh(){
		this.loadData('refresh');
	},
	onReachBottom(){
		this.loadData();
	},
	methods: {
		async loadCateList(fid, sid){
			let list = await this.$api.json('cateList');
			let cateList = list.filter(item=>item.pid == fid);
			
			cateList.forEach(item=>{
				let tempList = list.filter(val=>val.pid == item.id);
				item.child = tempList;
			})
			this.cateList = cateList;
		},
		async loadData(type='add', loading) {
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more'
			}
			
			let goodsList = await this.getGoodlist();
			if(type === 'refresh'){
				this.goodsList = [];
			}
			if(this.filterIndex === 3){
				goodsList.sort((a,b)=>b.renqi - a.renqi)
			}
			if(this.filterIndex === 2){
				goodsList.sort((a,b)=>{
					if(this.priceOrder == 1){
						return a.price - b.price;
					}
					return b.price - a.price;
				})
			}
			this.goodsList = this.goodsList.concat(goodsList);
			this.loadingType  = this.goodsList.length > this.count ? 'nomore' : 'more';
			if(type === 'refresh'){
				if(loading == 1){
					uni.hideLoading()
				}else{
					uni.stopPullDownRefresh();
				}
			}
		},
		async getGoodlist(){
			return new Promise((resolve, reject) => {
				uni.request({
					url:uni.getStorageSync('hostname') + '?action=index/product/index',
					data: {pn:this.pn,cateId:this.cateId,pcateId:this.pcateId,filterIndex:this.filterIndex,priceOrder:this.priceOrder,bondedwh_id:this.bondedwh_id,yhqid:this.yhqid,shopname:this.shopname},
					method:'POST',
					dataType:'json',
					header: {'content-type' : 'application/x-www-form-urlencoded'},
					success: (res) => {
						uni.hideLoading()
						if(res.data.code==0){
							this.pn = Number(this.pn)+Number(1);
							this.count = res.data.count;
							this.yhqDetail = res.data.yhqone;
							resolve(res.data.data);
						}else{
							this.loadingType = "nomore"
						}
					},
					fail: (err) => {
						reject('err')
					}
				});
			})
		},
		tabClick(index){
			this.pn = 0;
			if(this.filterIndex === index && index !== 2){
				return;
			}
			this.filterIndex = index;
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		navToDetailPage(item){
			let id = item.id;
			if(uni.getStorageSync('cbe_token')){
				uni.navigateTo({
					url: `/pages/product/product?id=${id}&utoken=${uni.getStorageSync('cbe_token')}`
				})
			}else{
				uni.navigateTo({
					url: `/pages/product/product?id=${id}`
				})
			}
		},
		stopPrevent(){}
	},
}
